export const isPhone = checkMobile();
function checkMobile() {
    return false;
}
function collapseOnPhone() {
    if (isPhone) {
        var imgs = document.getElementsByClassName("deembed-media");
        while (imgs.length > 0) {
            let img = imgs[0];
            let par = img.parentNode;
            //console.log(img.src+" @ "+par.tagName);
            if (par.tagName == "VIDEO") {
                let put = document.createElement("div");
                put.innerHTML = '<a href = "' + img.src + '">' + img.src + "</a>";
                par.parentNode.replaceChild(put, par);
            }
            else {
                //par.replaceChild(put, img);
                if (par.className == "video") {
                    var url = img.src.replace("embed/", "watch?v=");
                    par.innerHTML = '<a href = "' + url + '">' + url + "</a>";
                }
                else if (par.className != "header-image")
                    par.innerHTML = img.src + "<br>";
                else
                    par.innerHTML = "";
            }
            console.log("Hiding media '" + img.src + "'");
        }
        var text = document.getElementsByClassName("textarea");
        for (var i = 0; i < text.length; i++) {
            text[i].style.width = "33%";
        }
        let header = document.createElement("div");
        header.innerHTML = "<div style = 'text-align: center; width: 33%; margin: auto; border: 3px solid red;'><h2 style = 'color: red; margin: 12px;'>This site is not designed for use on phones and other handheld devices, nor is any of its content relevant on such platforms. You may continue to browse it this way if you wish, but it is recommended that you visit the site with a proper computer.</h2></div>";
        let navbar = document.getElementById("navbar");
        navbar.parentNode.insertBefore(header, navbar.nextSibling);
        let toc = document.getElementById("toc_container");
        toc.parentNode.removeChild(toc);
    }
}
function interceptDOM() {
    collapseOnPhone();
}
document.addEventListener("DOMContentLoaded", interceptDOM);
